<template>
  <v-app>
    <router-view class="d-flex justify-center"></router-view>
    <!-- 좌측네비게이션, 높이 고정 -->
    <v-navigation-drawer
      dark
      color="primary"
      app
      width="250px"
      height="100vh"
      floating
    >

    <!-- 타이틀 영역 -->
      <div class="d-flex align-center mx-2">
        <v-img
          alt=""
          class="shrink mr-2"
          contain
          src="./assets/img/gobongi.jpg"
          transition="scale-transition"
          width="40"
        />
        <div class="display-1 mx-2">Catalog</div>
      </div>
      
      <!-- 이미지 변경 버튼 -->
      <div class="d-flex justify-center mt-10">
      <v-btn
        href=""
        target="_blank"
        @click="changeImage"
        class="pa-md-4 mx-lg-auto"
      >
        <span class="mr-3">Change Image</span>
        <v-icon>mdi-autorenew</v-icon>
      </v-btn>
    </div>
    <!-- 키워드 검색 -->
    <div class="d-flex justify-center ml-2 mr-2 mt-10 align-center">
      <v-text-field 
        label="검색어"
        v-model="filterdata.keyword"
      ></v-text-field>
      <v-btn 
        @click="searchKeyword(filterdata.keyword)"
      >검색</v-btn>
    </div>

    <!-- 타입 설정 -->
    <div class="d-flex justify-center ml-2 mr-2 mt-10 align-center"> 
      <v-btn 
        class=""
        @click="changeType('all')"
      >전체</v-btn>
      <v-btn 
        class="ml-2"
        @click="changeType('1 day')"
      >하루용</v-btn>
      <v-btn 
        class="ml-2"
        @click="changeType('1 month')"
      >한달용</v-btn>
    </div>
     
    <!-- 이미지 사이즈 필터 -->
    <v-subheader class="pl-2 mt-10">
        Thumb size
      </v-subheader>
      <v-slider class="mt-5 ma-2"
        v-model="slider"
        :thumb-size="26"
        step="100"
        max="400"
        min="100"
        thumb-label="always"
        thumb-color="red"
      ></v-slider>

      <!-- 직경 선택 슬라이더 -->
    <v-subheader class="pl-2">
        gDIA
    </v-subheader>
    <div class="d-flex justify-center ma-2">
      <v-range-slider
        style="width: 200px"
        v-model = "filterdata.dia_range"
        :min=11
        :max=15
        thumb-label="always"
        thumb-color="red"
        :thumb-size="26"
        step="0.1"
      >
        <template v-slot:prepend>
          <v-text-field
            :value="filterdata.dia_range[0]"
            class="mt-0 pt-0"
            hide-details
            single-line
            type="number"
            style="width: 50px"
            @change="$set(filterdata.dia_range, 0, $event)"
          ></v-text-field>
        </template>
        <template v-slot:append>
          <v-text-field
            :value="filterdata.dia_range[1]"
            class="mt-0 pt-0"
            hide-details
            single-line
            type="number"
            style="width: 50px"
            @change="$set(filterdata.dia_range, 1, $event)"
            ></v-text-field>
        </template>
      </v-range-slider> 
    </div>
    <!-- 컬러 선택 -->
    <div>
      <div>
        <v-subheader class="pl-2">
        color selector
        </v-subheader>
      </div>
      <div class="d-flex justify-center">
        <v-img  
          class="shrink"
          contain
          src="./assets/img/select_color_9.png"
          transition="scale-transition"
          width="200"
          @click="selectColor"
          >
          <v-layout class="fill-height d-flex align-center justify-center">{{ mousePosition }}</v-layout>
        </v-img>
      </div>
    </div>
    
  <br>
  <div class="d-flex justify-center">ver 20220928 </div>



  </v-navigation-drawer>
  

    <v-main style="background-color:#FFF3F9">
      <!-- 탑으로 가기 버튼 -->
      <v-btn
          color="#FFC6E1"
          elevation="2"
          right bottom fab fixed
          @click="scrollTop"
        > <v-img
          alt=""
          class="mx-4"
          contain
          src="./assets/img/Central Heating.png"
          transition="scale-transition"
          width="30"
        />
      </v-btn>

      <!-- product list card -->
      <v-container>
        <h5 class="text-md-center">상품 수 : {{ filterdList.length }}</h5> 
        <v-row class="ma-1" >
          <div v-for="item, i in filterdList" :key="i" >
            <v-card class="ma-1">
            <div v-if="item.images[filterdata.imageNumber]!==undefined">
              <v-card>
                <v-img rounded
                  :src = item.images[filterdata.imageNumber].src
                  :width = filterdata.imageSize
                >                  
                <!-- 왜 폰트가 적용이 안될까? -->
                  <v-layout fill-height column ma-0>
                    <div class="d-flex justify-content-end">
                      <span class="mx-1"
                        style="-webkit-text-stroke: 0.2px #000000; font-size: 16px; letter-spacing: -0.02em; font-weight: 900; color:#ffffff">{{item.order_count}}</span>
                      <v-spacer></v-spacer>
                      <div class="d-flex">
                      <v-img v-if="item.lens_type==='하루용'"
                        class=""
                        contain
                        src="./assets/img/one-day.png"
                        height="22"
                      ></v-img> 
                      <v-img v-if="item.lens_type==='한달용'"
                        class=""
                        contain
                        src="./assets/img/one-month.png"
                        height="24"
                      ></v-img>
                      <span class="text-right mx-1"
                        style="-webkit-text-stroke: 0.2px #000000; font-size: 16px; letter-spacing: -0.02em; font-weight: 900; color:#ffffff">{{item.dia_inner+'mm'}}</span>
                      </div>
                    </div>
                    <v-spacer></v-spacer>
                    <span class="text-center text-truncate" 
                    style="-webkit-text-stroke: 0.2px #000000; font-size: 16px; letter-spacing: -0.1em; font-weight: 1000; color:#ffffff">{{item.title}}</span>
                  </v-layout>
                </v-img> 
              </v-card>
            </div>
            <p v-else>이미지가 없습니다.</p>
            </v-card>
          </div>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import itemList from './assets/products.js';

export default {
  name: 'App',
  computed: {
    filterdList(){
      return this.itemList.filter(item => 
      item.status === this.filterdata.status
      && item.variants[0].option1.includes(this.filterdata.color)
      && item.title.includes(this.filterdata.keyword)
      && item.variants[0].compare_at_price > this.filterdata.price
      && item.product_type !== "ETC"
      // 이 부분이 가장 걸림, ALL을 구현하기 위해 반대를 썼는데 함수를 바꿔야 할듯(리펙터링 고려) 아래의 방식으로 변경해서 해결
      && (this.filterdata.product_type == 'all' || item.product_type == this.filterdata.product_type)
      && item.dia_inner > this.filterdata.dia_range[0]
      && item.dia_inner < this.filterdata.dia_range[1]
      );
    },
  },
  data: () => ({
    itemList : itemList[0].records,
    filterdata : {
      'imageSize' : 200, 
      'status':'active',
      'imageNumber':0,
      'gdia':'all',
      'color' : '',
      'water' : 'all',
      'keyword' : '',
      'price' : '12000',
      'product_type' : 'all',
      'dia_range' : [11,15]
    },
    slider : "200",
    mousePosition : "click",
  }),
  methods: {
    changeImage(){
      this.filterdata.imageNumber===2 ? this.filterdata.imageNumber = 0 : this.filterdata.imageNumber += 1;
    },
    searchKeyword(keyword){
      console.log('keyword : ',keyword)
    },
    changeSize(size){
      this.filterdata.imageSize = size;
    },
    changeType(type){
      console.log('type : ', type)
      this.filterdata.product_type = type;
    },
    scrollTop(){
      window.scrollTo(0, 0);
    },
    selectColor(){
      this.mousePosition = (event.offsetX + "/" + event.offsetY)
    }
  },
  // 데이터 값 변경에 대한 모니터링, 기존값과 변경값을 인자로 받을 수 있다. 
  // 이미지 사이즈 변경 슬라이더의 값 모니터링
  watch : {
    slider(newValue){
      this.changeSize(newValue)
    },
  }
};
</script>
<style>
  #lateral .v-btn--example {
    bottom: 0;
    position: fixed;
    left:80%;
    top:50%;
  }
</style>
